<template>
    <div>
        <el-row :gutter="0">
            <el-col :lg="24" :md="24" :sm="24" :xs="24">
                <div class="banner_img">
                    <img src="../../static/img/cg.jpg" alt="">
                </div>
            </el-col>
        </el-row>
        <!--培训-->
        <div class="container">
            <el-row :gutter="10" style="margin-bottom: 50px;">
                <el-col :lg="24" :md="24" :sm="24" :xs="24">
                    <div class="title" id="title-college">
                        <h1>培训</h1>
                        <p>CULTVATE</p>
                        <hr>
                        <span></span>
                        <router-link :to="{ name: 'activity_more'}" class="r more_plus to"></router-link>
                    </div>
                    <el-row :gutter="10">
                        <el-col :lg="6" :md="6" :sm="12" :xs="24" class="college-show" v-for="(item,index) in lecture" :key="index">
                            <router-link :to="{ name: 'train_detail', params: { id: item.id} }" class="activitys_item">
                                <div @mouseover="show_activity(index)" @mouseout="hide_activity(index)">
                                    <h1 class="tc b f20 text-ellipsis">{{item['name']}}</h1>
                                    <p class="tc">培训时间：{{item['startAt'] | formatDate}}</p>
                                    <div class="top_line" style="width: 0px" :class="{ horizontal_line: activity_active[index] }"></div>
                                    <div class="bottom_line" style="width: 0px;" :class="{ horizontal_line: activity_active[index] }"></div>
                                    <div class="left_line" style="height: 0px;" :class="{ vertical_line: activity_active[index] }"></div>
                                    <div class="right_line" style="height: 0px;" :class="{ vertical_line: activity_active[index] }"></div>
                                </div>
                            </router-link>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
        </div>
        <!--优秀讲师-->
        <div style="background-color: #eee;padding-bottom: 50px;">
            <div class="container">
                <el-row :gutter="10">
                    <el-col :lg="24" :md="24" :sm="24" :xs="24">
                        <div class="title" id="title-teacher">
                            <h1>优秀讲师</h1>
                            <p>GREAT SPEAKERS</p>
                            <hr>
                            <span></span>
                            <router-link :to="{ name: 'trainList'}" class="r more_plus to"></router-link>
                        </div>
                        <el-row :gutter="40">
                            <el-col :lg="12" :md="12" :sm="12" :xs="24" v-for="(item, index) in teacher" :key="index" v-if="item['type'] == 4">
                                <div class="research-teacher">
                                    <img :src="item.photo" alt="">
                                    <div>
                                        <h4>指导老师:{{item.name}}</h4>
                                        <h3>{{item.title}}</h3>
                                        <p class="text-ellipsis-muti text-ellipsis-2">教师简介:{{item.intro}}</p>
                                    </div>
                                    <div>
                                        <router-link :to="{name:'tutorDetail', params: {id:item.id} }">讲师介绍</router-link>
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </div>
        </div>
        <!--活动专区-->
        <el-row class="activitys">
            <div class="container">
                <el-col :lg="24" :md="24" :sm="24" :xs="24">
                    <div class="title" id="title-activitys">
                        <h1>活动专区</h1>
                        <p>ACTIVITYZONE </p>
                        <hr>
                        <span></span>
                        <router-link :to="{ name: 'activity_more'}" class="r more_plus to"></router-link>
                    </div>
                    <el-row :gutter="10">
                        <el-col :xs="6" :sm="6" :md="6" :lg="6" v-for="(item, index) in activity" :key="index">
                            <router-link :to="{name:'train_detail', params: {id:item.id} }" class="activitys_item">
                                <div class="process abs">
                                    <div @mouseover="show_activity(index)" @mouseout="hide_activity(index)">
                                        <h1 class="tc b f20 text-ellipsis">{{item['name']}}</h1>
                                        <p class="tc">活动时间：{{item['startAt'] | formatDate}}</p>
                                        <div class="top_line" style="width: 0px" :class="{ horizontal_line: activity_active[index] }"></div>
                                        <div class="bottom_line" style="width: 0px;" :class="{ horizontal_line: activity_active[index] }"></div>
                                        <div class="left_line" style="height: 0px;" :class="{ vertical_line: activity_active[index] }"></div>
                                        <div class="right_line" style="height: 0px;" :class="{ vertical_line: activity_active[index] }"></div>
                                    </div>
                                </div>
                            </router-link>
                        </el-col>
                    </el-row>
                </el-col>
            </div>
        </el-row>
        <!--精彩瞬间-->
        <!-- <div style="background-color: #eee;padding-bottom: 50px;">
            <div class="container">
                <el-row :gutter="10">
                    <el-col :lg="24" :md="24" :sm="24" :xs="24">
                        <div class="title" id="title-splendid">
                            <h1>精彩瞬间</h1>
                            <p>SPLENDID MOMENT</p>
                            <hr>
                            <span></span>
                            <router-link :to="{ name: 'splendid_list'}" class="r more_plus to"></router-link>
                        </div>
                        <el-row :gutter="10">
                            <el-col :lg="6" :md="6" :sm="6" :xs="6" v-for="(item, index) in lecture" :key="index">
                                <router-link :to="{name:'train_detail', params: {id:item.id} }" class="research-splendid">
                                    <img :src="item.icon" alt="">
                                </router-link>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </div>
        </div> -->
        <!--footer-->
    </div>
</template>

<script>
    import api from "../axios/api.js";
    import {
        formatDate
    } from '../../static/js/date.js'
    export default {
        data() {
            return {
                lecture: "", //培训
                teacher: "", //优秀讲师
                activity: "", //活动专区
                highlight: "", //精彩瞬间
                activity_active: [true, false, false, false],
            };
        },
        methods: {
            setTronsApi() {
                api.Get("/pub/colleges").then(res => {
                    this.lecture = res["lecture"];
                    this.highlight = res["highlight"];
                    this.activity = res["activity"];
                    this.teacher = res["teacher"];
                });
            },
            show_activity(index) {
                var activity_item = document.querySelectorAll(".activitys_item");
                for (let i = 0; i < activity_item.length; i++) {
                    if (activity_item[i] != activity_item[index]) {
                        this.$set(this.activity_active, i, false);
                    } else {
                        this.$set(this.activity_active, i, true);
                    }
                }
            },
            hide_activity(index) {
                var activity_item = document.querySelectorAll(".activitys_item");
                for (let i = 0; i < activity_item.length; i++) {
                    this.$set(this.activity_active, i, false);
                }
            }
        },
        created() {
            this.setTronsApi();
        },
        filters: {
            formatDate(time) {
                let date = new Date(time)
                return formatDate(date, 'yyyy-MM-dd')
            }
        },
    };
</script>

<style scoped>
    /*培训*/
    .college-show img {
        width: 100%;
    }
    .college-show div {
        /* box-shadow: 0 0 10px #ddd; */
        /* margin-top: -5px; */
        /* padding: 5px; */
    }
    .college-show div h6 {
        color: #666;
        font-size: 14px;
        line-height: 30px;
    }
    .college-show div p {
        color: #999;
        font-size: 12px;
        line-height: 20px;
    }
    .college-show {
        margin-bottom: 20px;
    }
    /*title*/
    .title {
        text-align: center;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    .title h1 {
        color: #0089e3;
        font-size: 18px;
        font-weight: 600;
    }
    .title p {
        color: #ddd;
        font-size: 10px;
    }
    .title hr {
        width: 30%;
        border: 1px solid #454b60;
    }
    .title span {
        display: block;
        border: 2px solid #0089e3;
        width: 10%;
    }
    .title a.to {
        margin-top: -41px;
    }
    #title-college {
        position: relative;
    }
    #title-college span {
        position: absolute;
        top: 48px;
        left: 45%;
    }
    /*优秀讲师*/
    #title-teacher {
        position: relative;
    }
    #title-teacher span {
        position: absolute;
        top: 48px;
        left: 45%;
    }
    .research-teacher {
        background-color: #0068b7;
        padding-top: 20px;
        margin-top: 50px;
        position: relative;
        min-height: 220px;
    }
    .research-teacher img {
        width: 30%;
        margin-right: auto;
        margin-left: auto;
        display: block;
        position: absolute;
        top: -28px;
        left: -28px;
    }
    .research-teacher div {
        margin-left: 30px;
        margin-right: 30px;
        text-align: center;
        color: #fff;
        margin-top: 25px;
    }
    .research-teacher div h4 {
        font-size: 16px;
        font-weight: normal;
        line-height: 30px;
        margin-left: -70px;
    }
    .research-teacher div h3 {
        font-size: 14px;
        font-weight: normal;
        line-height: 30px;
        margin-left: -60px;
    }
    .research-teacher div p {
        width: 60%;
        margin-left: 30%;
        font-size: 12px;
        font-weight: normal;
        line-height: 20px;
        margin-top: 10px;
        height: 40px;
    }
    .research-teacher a {
        width: 100px;
        line-height: 26px;
        border-radius: 12px;
        background-color: #f53436;
        color: #fff;
        font-size: 14px;
        border: none;
        margin: 20px auto;
    }
    /*活动专区*/
    #title-activitys {
        position: relative;
    }
    #title-activitys span {
        position: absolute;
        top: 48px;
        left: 45%;
    }
    .activitys_item img {
        width: 100%;
    }
    /*精彩瞬间*/
    #title-splendid {
        position: relative;
    }
    #title-splendid span {
        position: absolute;
        top: 48px;
        left: 45%;
    }
    .research-splendid {
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }
    .research-splendid:hover {
        box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.1);
    }
    .research-splendid img {
        width: 100%;
    }
    @media screen and (min-width: 992px) {
        .right div {
            float: right;
            height: 60px;
            width: 50px;
        }
    }
    @media screen and (max-width: 768px) {
        .arrows-splendid div {
            overflow: hidden;
            margin-top: 20%;
        }
        .right div {
            float: right;
            height: 60px;
            width: 28px;
        }
    }
</style>


